import * as jQuery from "jquery";

// They key to store the country to not hit the service every time
let countryKey = 'country_code';

// The Json returned
interface IP {
    success: boolean,
    ip: string,
    country2: string,
    country3: string,
    country: string
}

/**
 *
 * @returns {string} the country code on two characters
 */
export function getCountry() {

    let countryValue: string = localStorage.getItem(countryKey);

    let consent: boolean = ( countryValue == null );
    if (consent) {
        jQuery.ajax(
            'https://api.gerardnico.com/ip',
            {
                success: function (data: IP) {
                    let countryCode = data.country2;
                    localStorage.setItem(countryKey, countryCode);
                    return countryCode;
                },
                error: function() {
                    // An error occurred, trying the fallback
                    jQuery.ajax(
                        'https://ip2c.org/self',
                        {
                            success: function (data: string) {
                                let countryCode = data.split(";")[1];
                                localStorage.setItem(countryKey, countryCode);
                                return countryCode;
                            }
                        }
                    )
                }
            }
        )
    } else {
        return countryValue;
    }

}





